import React, { Component } from "react";
import {
  loginBg,
  prepStarLogo,
  prepladderLogo,
  facebook,
} from "../common/Icon";
import Link from "../../src/common/Link";
import Fetch from "../common/Fetch";
import { withContext } from "../context/AppContext";
import Terms from "../component/Terms";

class Login extends Component {
  state = {
    email: "",
    password: "",
    isChecked: false,
    err: "",
    showTermsPopup: false,
  };

  componentDidMount() {
    console.log(this.props);
  }

  // varifyUser = () => {
  //   Fetch("login/verify.php");
  // };

  onChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  onSubmit = (e) => {
    e.preventDefault();
    const { email, password } = this.state;

    Fetch("login/login.php", { email, password })
      .then((d) => {
        if (d.status) {

          localStorage.setItem("data", JSON.stringify(d.data));
          setTimeout(() => {
            if(d.data.isShowAgreement){
              this.setState({ showTermsPopup: true });
            }else{
              this.afterLoginRedirect()
            }
          }, 1000);
           
          
        }
      })
      .catch((e) => {
        console.log(e);
      });
  };

  afterLoginRedirect=()=>{
    this.props.context.toggleUserLogin(true);
    this.props.history.push("/");
  }

  onChangeCheckBox = (e) => {
    this.setState({ isChecked: e.target.checked });
  };

  onAcceptTerms = (e) => {
    e.preventDefault();
    if (this.state.isChecked) {
      Fetch("login/acceptagreement.php",{agreement:1}).then((d) => {
        if (d.status) {
          this.afterLoginRedirect()
        }
      });
    } else {
      this.setState({ err: "Please Check terms and conditions" });
    }
  };

  render() {
    const { showTermsPopup } = this.state;

    return (
      <div className="login">
        <div className="login_header logo">
          <Link to="/">
            <img src={prepStarLogo} alt="PrepStar" />
          </Link>
        </div>
        <div className="loginContainer">
          <div className="row">
            <div className="col-lg-5 col-xl-4 col-12">
              <h1 className="main_hed">Sign In</h1>
              <form className="login_form" onSubmit={this.onSubmit}>
                <div class="form-group">
                  <input
                    type="email"
                    name="email"
                    onChange={this.onChange}
                    value={this.state["email"]}
                    id="loginName"
                    class="form-control"
                    placeholder="Email id"
                    required
                  />
                </div>

                <div class="form-group">
                  <input
                    type="password"
                    onChange={this.onChange}
                    value={this.state["password"]}
                    name="password"
                    id="loginPswd"
                    class="form-control"
                    placeholder="Password"
                    required
                  />
                </div>
                {/* <div className="forgot_txt"><a href="/" className="forgot">Forgot Password?</a></div> */}
                <div className="chkbx_btn">
                  <div class="form-group form-check mb-0">
                    <input type="checkbox" id="exampleCheck1" />
                    <span class="form-check-label">Remember me</span>
                  </div>
                  <div className="site_btn form_btn">
                    <button
                      type="submit"
                      className="btnRadius bluebg text-uppercase"
                    >
                      Login
                    </button>
                  </div>
                </div>
                {/* <div className="or">or</div>
                            <div className="loginFB">
                                <button type="submit" className="fb">Sign up with Facebook</button>
                            </div> */}
              </form>
            </div>
            <div className="col-lg-7 col-xl-8 text-right right_img d-none d-lg-block">
              <img src={loginBg} alt="Login" />
            </div>
            <div className="col-12 login_footer">
              <div className="row">
                <div className="footer_left col-md-6">
                  <ul className="list-unstyled mb-0 d-flex flex-row">
                    <li>Term &amp; Conditions</li>
                    <li>Contact Us</li>
                  </ul>
                </div>
                <div className="footer_right col-md-6">
                  <span>Powered by</span>
                  <img src={prepladderLogo} alt="prepladder" />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* popup section start */}
        {/* <div className="popup popup1">
                <div className="popup_box">
                    <div className="d-flex align-items-center">
                        <div className="popup_txt">
                            <h1>Welcome!</h1>
                            <h2>Please fill your delivery address to claim your goodie kit</h2>
                            <div className="popup_Btn site_btn">
                                <a href="#" className="btnRadius bluebg text-uppercase">Fill Now</a>
                            </div>
                            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                        </div>
                        <div className="popup_img text-right">
                        <img src={loginBg} alt="" />
                        </div>
                    </div>
                </div>
            </div> */}
        {/* <div className="popup popup2">
                <div className="popup_box">
                    <div className="d-flex align-items-center">
                        <div className="popup_txt">
                            <h1>Congratulations!</h1>
                            <h2>You are now a Platinum PrepStart.</h2>
                            <p>Keep Shining!</p>
                            <div className="popup_Btn site_btn">
                                <a href="#" className="btnRadius bluebg text-uppercase">Claim Revard</a>
                            </div>                            
                        </div>
                        <div className="popup_img text-right">
                        <img src={loginBg} alt="" />
                        </div>
                    </div>
                </div>
            </div> */}
        {/* <div className="popup popup3">
                <div className="popup_box">
                    <div className="d-flex align-items-center">
                        <div className="popup_txt">
                            <h1>Congratulations!</h1>
                            <h2>You are now a Gold PrepStart.</h2>
                            <p>Keep Shining!</p>
                            <div className="popup_Btn site_btn">
                                <a href="#" className="btnRadius bluebg text-uppercase">Claim Revard</a>
                            </div>                            
                        </div>
                        <div className="popup_img text-right">
                        <img src={loginBg} alt="" />
                        </div>
                    </div>
                </div>
            </div> */}
        {/* <div className="popup popup4">
                <div className="popup_box">
                    <div className="d-flex align-items-center">
                        <div className="popup_txt">
                            <h1>Congratulations!</h1>
                            <h2>You are now a Silver PrepStart.</h2>
                            <p>You just earned 10 more Shine Points</p>
                            <div className="popup_Btn site_btn">
                                <a href="#" className="btnRadius bluebg text-uppercase">Claim Revard</a>
                            </div>                            
                        </div>
                        <div className="popup_img text-right">
                        <img src={loginBg} alt="" />
                        </div>
                    </div>
                </div>
            </div> */}

        {showTermsPopup ? (
          <div className="popup termConditions">
            <div className="popup_box">
              <Terms
                onChange={this.onChangeCheckBox}
                onSubmit={this.onAcceptTerms}
                checked={this.state.isChecked}
                isShowTermsCheckbox
                errMsg={this.state.err}
              />
            </div>
          </div>
        ) : null}

        {/* End */}
      </div>
    );
  }
}

export default withContext(Login);
