import React, { Component } from 'react';
import InfiniteScroll from 'react-infinite-scroller';
import Fetch from '../common/Fetch';
import {withContext} from '../context/AppContext'
import Loader from '../component/Loader';

const maxPerPage=10;

class ShinePoints extends Component {

    state={
        isLoading:true,
        data:{
            pointSummery:[],
            total:{
                title:"",
                totalPoint:"",
                totalRecords:""
            }
        },
        counter:0
    }

    componentDidMount(){
        window.scrollTo(0,0);
        this.fetchData()
    }

    fetchData=(counter=0)=>{
        // const {setApiCache,getApiCache}=this.props.context;
        // if(getApiCache(`pointssummery`)){
        //     this.setState({data:getApiCache(`pointssummery`),isLoading:false})
        //   }
        Fetch('points/pointssummery.php',{maxPerPage:maxPerPage,counter:counter}).then(d=>{
            // console.log(d);
            if(d.status){
                const copySummTableData = counter===0 ? d.data.pointSummery : [...this.state.data.pointSummery,...d.data.pointSummery]
                this.setState({data:{...d.data,pointSummery:copySummTableData},isLoading:false})
                // setApiCache(`pointssummery`,d.data)
            //   setData(d.data)
            //   setApiCache('pointssummeryDash',d.data)
            //   setLoading(0)
            }
        })
    }

    onLoadNewData=(counter)=>{
        console.log(counter);
        this.setState({counter})
        this.fetchData(counter)
    }

    render() {
        // console.log(this.state);
        const {data:{pointSummery,total:{title,totalPoint}="",totalRecords},isLoading,counter}=this.state;
        console.log(pointSummery);
        
        return (
            isLoading
            ?
            <Loader/>
            :
            <div className="shinePoints">
                <h1 class="main_hed">Shine Points</h1>
                <div className="bg_white">
                    <div className="table-responsive">
                <table className="shineTable">
                <tbody>
                <tr>
                    <td ><h2 className="sub_hed mb-0">{title}</h2></td>
                    <td align="" className="">Date</td>
                    <td align="center" > Total Points : <span style={{display:'inline-block'}} className="green">{totalPoint}</span></td>
                </tr>
                </tbody>

                <InfiniteScroll
                    pageStart={counter}
                    loadMore={this.onLoadNewData}
                    hasMore={Math.ceil(totalRecords/maxPerPage)  !== counter }
                    loader={<div className="loader" key={0}>Loading ...</div>}
                    initialLoad={false}
                    element={'tbody'}
                >
                {
                    pointSummery && pointSummery.length
                    ?
                    pointSummery.map((d,i)=>(
                        <tr>
                            <td>{i+1} : {d.label}</td>
                            {/* <td>Sec: Sourabh Gupta</td> */}
                            <td>{d.date}</td>
                            <td align="center"><span className={`small ${Number(d.points) > 0 ? "green" :"red"}`}>{d.points}</span></td>
                        </tr>
                    ))
                    : null
                }
               </InfiniteScroll>
                
               
                {/* <tr>
                <td colSpan={4} align="center"><a href="#" className="table_vm">View More</a></td>
                </tr> */}
               
                </table>
                </div>
                </div>
                </div>
        );
    }
}

export default withContext(ShinePoints);

