import axios from "axios";
import UrlList from "./Urls";

export const userDataObj = JSON.parse(localStorage.getItem("data") ? localStorage.getItem("data") : `{}`);


// console.log('pl');


export default function Fetch(endPoint, extendedParams, options) {

  const user = localStorage.getItem("data");
  const userData = user && user.length && JSON.parse(user);

  const commonParams = {
    apikey: userData?.apikey ,
    email:userData?.email,
    // email:"test@gmail.com",  
    // apikey: "123456",
    // ...data,
    // appID:1,
    // "platform":"web",
    // "version":"1"
  };
  const indexOfUrl = options?.indexOfUrls;
  const url = options?.url;
  const method = options?.method;
  const commonParam = options?.params;

  return axios[method ? method : "post"](
    url ? url : indexOfUrl ? UrlList[indexOfUrl] : UrlList[0] + endPoint,
    commonParam
      ? commonParam
      : {
          ...commonParams,
          ...extendedParams,
        }
  )
    .then((d) => {
      if(d.data?.response){
        return d.data.response;
      }
      return d.data.response;
    })
    .catch((e) => Promise.reject(e));
}
