import React, { Component } from "react";
import { imgAvatar, downArrow } from "../common/Icon";
import Fetch from '../common/Fetch';
import { useEffect } from "react";
import { useState } from "react";

class Profile extends Component {

  state={
    userData:{
      addresses:[],
      bank:[],
      isAddressEmpty: false,
      isBankEmpty: false,
      isProfileEmpty: false,
      profile:{
        college:"",
        email:"",
        name:"",
        phone:"",
        year:""
      }
    },
    active:'',
    tabsData:{
      activeBankData:{},
      activeAddressData:{}
    }

  }

  componentDidMount(){
    Fetch('login/viewprofile.php').then(d=>{
      // console.log(d);
      this.setState({userData:d.data})
    })
  }

  onUpdateProfileData=(data)=>{
    const newData={...this.state.userData.profile,...data};
    this.setState({userData:{...this.state.userData,profile:newData}})
  }

  onUpdateAddressData=(data,index)=>{
    const newAddress=[...this.state.userData.addresses]
    newAddress[index]=data
    this.setState({userData:{...this.state.userData,addresses:newAddress}})
  }

  onUpdateBankData=(data,index)=>{
    const newBank=[...this.state.userData.bank]
    newBank[index]=data
    this.setState({userData:{...this.state.userData,bank:newBank}})
  }


  changeEditMode=(activeMode,data={})=>{
    this.setState({active:activeMode,tabsData:{...this.tabsData,...data}})
  }


  render() {

    const {userData:{profile,isAddressEmpty,isBankEmpty,isProfileEmpty,addresses,bank},active,tabsData}=this.state;
    console.log(this.state);
    

    return (
      <div className="right_links" id="right_collapse">
        <div className="card">
          <a
            className="card_active"
            data-toggle="collapse"
            href="#profile"
            aria-expanded="true"
          >
            Profile
          </a>
          <div
            id="profile"
            className="collapse show"
            data-parent="#right_collapse"
          >
            <div className="card-body">
              <div className="profile">
               
                <div className="profile_img text-center">
                  <img src={imgAvatar} alt="profile" />
                  {/* <span className="img_active" /> */}
                  <span className="share_fb">Share (fb)</span>
                  <p>Working with us since 08/04/2015</p>
                </div>
              </div>
              {
                active==='profile' || isProfileEmpty
                ?
                <AddProfile onUpdateProfileData={this.onUpdateProfileData} onEdit={this.changeEditMode} defaultData={profile}/>
                :
                <ShowProfile {...profile} onEdit={this.changeEditMode} />
              }
              
            </div>
          </div>
        </div>
        <div className="card">
          <a className="card_active" data-toggle="collapse" href="#myAddress">
            My Address
          </a>
          <div
            id="myAddress"
            className="collapse"
            data-parent="#right_collapse"
          >
            <div className="card-body">
              {
                active==='address' || isAddressEmpty
                ?
                <AddAddress tabsData={tabsData} onUpdateAddressData={this.onUpdateAddressData} onEdit={this.changeEditMode} /> 
                :
                <ShowAddress dataArr={addresses}  onEdit={this.changeEditMode} />
              }
            
              {/* Detail section*/}
             
            </div>
          </div>
        </div>
        <div className="card">
          <a className="card_active" data-toggle="collapse" href="#bankInfo">
            Bank Account info
          </a>
          <div id="bankInfo" className="collapse" data-parent="#right_collapse">
            <div className="card-body">

            {
                active==='bank' || isBankEmpty
                ?
                <AddBankAcc tabsData={tabsData} onEdit={this.changeEditMode} onUpdateBankData={this.onUpdateBankData}/>
                :
                <ShowBankDetails dataArr={bank} onEdit={this.changeEditMode} />
              }

              
              
            </div>
          </div>
        </div>
        <div className="card">
          <a className="card_active" data-toggle="collapse" href="#orderInfo">
            Order info
          </a>
          <div
            id="orderInfo"
            className="collapse"
            data-parent="#right_collapse"
          >
            <div className="card-body">
              <div className="orderInfo" id="order_collapse">
                <OrderCard />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Profile;

const ShowProfile = ({college,email,name,phone,year,onEdit}) => {
  
  return (
    <div className="profile_details details">
      <div className="d-flex">
        <div className="details_content">
          <h3>{name}</h3>
          <p>Mobile: {phone}</p>
          <p>Email id: {email}</p>
          <p>College: {college}</p>
          <p>Year: {year}</p>
        </div>
        <div className="site_btn details_right">
          <a className="btnBorder" onClick={()=>onEdit('profile',{})}>
            Edit
          </a>
        </div>
      </div>
    </div>
  );
};

const ShowBankDetails = ({dataArr,onEdit}) => {
  return (
    <div className="profile_details details">
      {
        dataArr && dataArr.length
        ?
        dataArr.map((d,i)=>(
          <div className="d-flex">
            <div className="details_content">
              <p>Name on A/C: {d.accountName}</p>
              <p>A/C No: {d.accountNumber}</p>
              <p>IFSC Code: {d.IFSC}</p>
            </div>
            <div className="site_btn details_right">
              <button className="btn btnBorder" onClick={()=>onEdit('bank',{activeBankData:dataArr[i],currentBankIndex:i})}>
                Edit
              </button>
            </div>
          </div>
        ))
        : null
      }
    
    </div>
  );
};

const ShowAddress = ({dataArr,onEdit}) => {
  return (
    dataArr && dataArr.length
    ? 
    dataArr.map((d,i)=>(
      <div key={i} className="profile_details details">
      <div className="d-flex">
        <div className="details_content">
          <p>{d?.address}</p>
          <p>{d?.city} </p>
          <p>{d?.locality }</p>
          <p>{d?.state} , {d?.zip}</p>
          {
            d?.phone &&  <p>Phone : {d?.phone}</p>
          }
        
        </div>
        <div className="site_btn details_right">
          <button className="btnBorder" onClick={()=>onEdit('address',{activeAddressData:dataArr[i],currentAddressIndex:i})}>
            Edit
          </button>
        </div>
      </div>
      <div className="d-flex align-items-center">
        <div className="details_content">
          <p className='red'>Mobile: pending from backend</p>
        </div>
        <div className="site_btn details_right">
          <a className="btnRadius btn-greybg">
            {
              d.type===0 ? "Home" :"Office"
            }
            {/* Home */}
          </a>
        </div>
      </div>
      {/* <div className="add_new">
        <a href="/" className="green">
          + Add New Address
        </a>
      </div> */}
    </div>
    ))
   
    : null
  );
};

const OrderCard = () => {
  const [data,setData]=useState([]);

  useEffect(()=>{
    Fetch('profile/order.php').then(d=>{
        console.log(d);
        if(d.status){
          setData(d.data.orderList)
        }
    })
  },[])
  return (
    data && data.length
    ?
    data.map((d,i)=>(
      <div class="media" key={i}>
        <div className="orderImg mr-4"></div>
        <div class="media-body">
          <h3 className="mt-3">{d.name}</h3>
          <p>Ordered : {d.created_at}</p>
          <p>Order ID : {d.orderID}</p>
        </div>
        {/* <div className="downArrow">
          <img src={downArrow} />
        </div> */}
      </div>
    ))
    : <div className="media">
      <div className="media-body">
        <p> No Order Found !</p>
       
      </div>
     
    </div>
    
  );
};



class AddBankAcc extends Component {

  state={
    err:"",
    currentBankIndex:0
  }

  componentDidMount(){
    const {activeBankData,currentBankIndex}=this.props.tabsData;
    if(activeBankData){
      const {IFSC,accountName,accountNumber}=activeBankData;
      this.setState({IFSC,accountName,accountNumber,currentBankIndex})
    }
  }


  onChange=(e)=>{
    const {name,value}=e.target;
    this.setState({[name]:value})
  }

  onSubmit=(e)=>{
    e.preventDefault();
    const {IFSC,accountName,accountNumber,accountNumberConfirm,currentBankIndex}=this.state;
    if(!accountName || !accountName.trim()){
      return this.setState({err:"Name not valid"})
    }

    if(accountNumber !== accountNumberConfirm || !accountNumber.trim()){
      return this.setState({err:"Account No. not match"})
    }

    if(!IFSC.trim() || !IFSC.length){
      return this.setState({err:"IFSC not valid"})
    }
    this.setState({err:""})
    
    const parms={IFSC,accountName,accountNumber};

    Fetch('profile/account.php',parms).then(d=>{
      console.log(d);
      if(d.status){
        // alert('address added')

        this.props.onEdit('')
        this.props.onUpdateBankData(parms,currentBankIndex)
      }
      
    })
  }

  render() {

    console.log(this.state);
    const {err}=this.state;
    
    return (
      <form className="form" onSubmit={this.onSubmit} autocomplete="new-password">
      <div className="row">
        <div className="form-group col-12">
          <lable htmlFor="accName">Name on Account</lable>
          <input
          required
            type="text"
            className="form-control"
            name="accountName"
            autocomplete="new-password"
            id="accName"
            onChange={this.onChange} value={this.state['accountName']}
          />
        </div>
        <div className="form-group col-12">
          <lable htmlFor="accNo">Account No.</lable>
          <input required type="text" onChange={this.onChange} value={this.state['accountNumber']} className="form-control" autocomplete="new-password"  name="accountNumber" id="accNo" />
        </div>
        <div className="form-group col-12">
          <lable htmlFor="accNo">Confirm Account No.</lable>
          <input required type="password"  onChange={this.onChange} value={this.state['accountNumberConfirm']} className="form-control" autocomplete="new-password"   name="accountNumberConfirm" id="accNo" />
        </div>
        <div className="form-group col-6">
          <lable htmlFor="ifscCode">IFSC Code</lable>
          <input
          required
          onChange={this.onChange} value={this.state['IFSC']}
            type="text"
            className="form-control"
            name="IFSC"
            autocomplete="new-password"
            id="ifscCode"
          />
        </div>
        <div className="col-12">
          {
            err &&   <p className='red'>{err}</p>
          }
         
          <div className="site_btn form_btn text-right">
         
            <button type="submit" className="btnRadius">
              Save
            </button>
            <button type="button" style={{marginLeft:"10px"}} onClick={()=>this.props.onEdit("")} className="btnRadius">
              Cancel
            </button>
          </div>
        </div>
      </div>
    </form>
    );
  }
}



class AddAddress extends Component {

  state={
    addressType:"0",
    addressID:"",
  }

  componentDidMount(){
    // console.log(this.props.tabsData?.activeAddressData);
    const {activeAddressData,currentAddressIndex}=this.props.tabsData;
    if(activeAddressData){
      const {address,locality,city,state,type,addressID,zip,}=activeAddressData;
      this.setState({address:address,addressType:String(type),city,state,town:locality,pincode:zip,addressID,currentAddressIndex})
    }
    
  }

  onChange=(e)=>{
    const {name,value}=e.target;
    this.setState({[name]:value})
  }

  onSubmit=(e)=>{
    e.preventDefault();
    const {address,addressType,city,pincode,state,town,addressID,currentAddressIndex,phone}=this.state;
    const parms={addressID,address:address,zip:pincode,locality:town,city,state:state,type:addressType,phone};

    Fetch('profile/address.php',parms).then(d=>{
      console.log(d);
      this.props.onEdit('')
      this.props.onUpdateAddressData(parms,currentAddressIndex)
      // if(d?.status){
      //   this.props.onEdit('')
      //   this.props.onUpdateAddressData(parms)
      // }else{
      //   alert("err from back end")
      //   this.props.onEdit('')
      // }
      // alert('pending')
     
    })

  }

  render() {
    // console.log(this.state);
    
    return (
      <form className="form" onSubmit={this.onSubmit}>
      <div className="row">
        <div className="form-group col-12">
          <span>Type of Address</span>
          <label class="radio-inline">
            <input onChange={this.onChange} checked={this.state['addressType']==="0"} type="radio" name="addressType" value='0' />
            Home
          </label>
          <label class="radio-inline">
            <input  onChange={this.onChange}  checked={this.state['addressType']==="1"} type="radio" name="addressType" value='1' />
            Office
          </label>
        </div>
        <div className="form-group col-12">
          <lable htmlFor="addr">
            Address (House No., Building, Street, Area)*
          </lable>
          <input type="text" required  onChange={this.onChange} value={this.state['address']} className="form-control" name="address" id="addr" />
        </div>
        <div className="form-group col-6">
          <lable htmlFor="addrTown">Localty/Town</lable>
          <input
            type="text"
            className="form-control"
            name="town"
            id="addrTown"
            onChange={this.onChange}
            value={this.state['town']}
            required
          />
        </div>
        <div className="form-group col-6">
          <lable htmlFor="Pincode">Pincode</lable>
          <input
          required
            type="text"
            className="form-control"
            name="pincode"
            id="Pincode"
            onChange={this.onChange}
            value={this.state['pincode']}
          />
        </div>
        <div className="form-group col-6">
          <lable htmlFor="addrCity">City/District</lable>
          <input
            required
            type="text"
            className="form-control"
            name="city"
            id="addrCity"
            onChange={this.onChange}
            value={this.state['city']}
          />
        </div>
        <div className="form-group col-6">
          <lable htmlFor="addrState">State</lable>
          <input
            required
            type="text"
            className="form-control"
            name="state"
            id="addrState"
            onChange={this.onChange}
            value={this.state['state']}
          />
        </div>

        <div className="form-group col-12">
          <lable htmlFor="addrState">State</lable>
          <input
            required
            type="text"
            className="form-control"
            name="phone"
            id="addrState"
            onChange={this.onChange}
            value={this.state['phone']}
          />
        </div>

        <div className="col-12">
          <div className="site_btn form_btn text-right">
            <button type="submit" className="btnRadius">
              Save
            </button>
          </div>
        </div>
      </div>
    </form>
    );
  }
}





class AddProfile extends Component {

  state={
    ...this.props.defaultData
  }

  onChange=(e)=>{
    const {name,value}=e.target;
    this.setState({[name]:value})
  }
  
  onSaveAddress=(e)=>{
    e.preventDefault();
    Fetch('profile/profile.php',this.state).then(d=>{
      if(d.status){
        this.props.onUpdateProfileData(this.state)
        this.props.onEdit('')
      }
    })
  }

  // college,email,name,phone,year

  render() {
    console.log(this.state);
    
    return (
      <form className="form" onSubmit={this.onSaveAddress}>
      <div className="row">
        <div className="form-group col-12">
          <lable htmlFor="fullName">Full Name</lable>
          <input
            onChange={this.onChange} 
            value={this.state['name']}
            type="text"
            className="form-control"
            name="name"
            id="fullName"
          />
        </div>
        <div className="form-group col-4">
          <lable htmlFor="phone">Phone No.</lable>
          <input   onChange={this.onChange} value={this.state['phone']} type="tel" className="form-control" name="phone" id="phone" />
        </div>
        <div className="form-group col-8">
          <lable htmlFor="email">Email</lable>
          <input
          onChange={this.onChange} value={this.state['email']}
          readOnly
            type="email"
            className="form-control"
            name="email"
            id="email"
          />
        </div>
        <div className="form-group col-7">
          <lable htmlFor="college">College</lable>
          <input
          onChange={this.onChange} value={this.state['college']}
            type="text"
            className="form-control"
            name="college"
            id="college"
          />
        </div>
        <div className="form-group col-5">
          <lable htmlFor="year">Year</lable>
          <input type="text" onChange={this.onChange} value={this.state['year']} className="form-control" name="year" id="year" />
        </div>
        <div className="col-12">
          <div className="site_btn form_btn text-right">
            <button type="submit" className="btnRadius">
              Save
            </button>
          </div>
        </div>
      </div>
    </form>
    );
  }
}



