import React, { Component, createContext } from "react";
import Fetch,{userDataObj} from "../common/Fetch";
import {history} from '../common/History';
// import {withRouter} from 'react-router-dom';

export const MainContext = createContext();

const setProperty = (key, value) =>
  document.documentElement.style.setProperty(key, value);

class AppContext extends Component {
  state = {
    crrOpen: true,
    crlOpen: true,
    isLogin: false,
    authLoading: true,
    apiCache:{}
  };

  componentDidMount() {
    if(userDataObj && userDataObj?.email){
      this.varifyUser();
    }else{
      this.setState({ authLoading: false });
    }
   
   
    if (window.innerWidth <= 991) {
      this.onToogleLeftCr();
    }
    window.addEventListener("resize", () => {
      if (window.innerWidth <= 991) {
          this.resetSideBars()
      }
    });
  }

  resetSideBars=()=>{
    this.setState({ crrOpen: false, crlOpen: false });
    setProperty("--crl-width", "0rem");
    setProperty("--crl-left", "-23rem");
    setProperty("--crr-width", "0rem");
    setProperty("--crr-right", "40rem");
    document.querySelector('.bg').style.display='none'
  }

  setApiCache=(key,value)=>{
    this.setState({apiCache:{...this.state.apiCache,[key]:value}})
  }

  getApiCache=(key)=>{
    const {apiCache}=this.state;

    if(key in apiCache){
      return apiCache[key]
    }

    return;
  }


  varifyUser = () => {
    this.setState({ authLoading: true });
    Fetch("login/verify.php")
      .then((d) => {
        if (!d.status) {
          this.onLogout();
        } else {
          this.setState({ isLogin: true });
          // window.history.pushState({}, null, '/');
          // console.log(window.location.pathname);
          history.push('/')
          // if (window.location.pathname === "/login") {
          //   window.location = "/";
          // }
          // window.location='/'
        }
        this.setState({ authLoading: false });
      })
      .catch((e) => {
        this.setState({ authLoading: false });
        this.onLogout();
      });
  };

  toggleUserLogin = (bool) => {
    this.setState({ isLogin: bool });
  };

  onLogout = () => {
    localStorage.clear();
    this.setState({ isLogin: false });
    // window.location='/login'
  };

  onToogleRightCr = () => {
    this.setState({ crrOpen: !this.state.crrOpen });
    if (!this.state.crrOpen) {
      setProperty("--crr-width", "0rem");
      setProperty("--crr-right", "40rem");
    } else {
      if(window.innerWidth <= 991) {
        document.querySelector('.bg').style.display='block'
      }
     
      setProperty("--crr-width", "40rem");
      setProperty("--crr-right", "0rem");
    }
  };

  onToogleLeftCr = () => {
    this.setState({ crlOpen: !this.state.crlOpen });
    if (this.state.crlOpen) {
      setProperty("--crl-width", "0rem");
      setProperty("--crl-left", "-23rem");
    } else {
      if(window.innerWidth <= 991) {
        document.querySelector('.bg').style.display='block'
      }
      setProperty("--crl-width", "23rem");
      setProperty("--crl-left", "0rem");
    }
  };

  render() {
    console.log(this.state);

    return (
      <MainContext.Provider
        value={{
          ...this.state,
          onLogout: this.onLogout,
          onToogleRightCr: this.onToogleRightCr,
          onToogleLeftCr: this.onToogleLeftCr,
          toggleUserLogin: this.toggleUserLogin,
          setApiCache:this.setApiCache,
          getApiCache:this.getApiCache,
          resetSideBars:this.resetSideBars
        }}
      >
        {this.props.children}
      </MainContext.Provider>
    );
  }
}

export default AppContext;

export const withContext = (Components) => (props) => {
  return (
    <MainContext.Consumer>
      {(value) => <Components context={value} {...props} />}
    </MainContext.Consumer>
  );
};
