import React, { Component,useState, useEffect } from "react";
import Fetch from "../common/Fetch";
import { bronze, gold, share, silver } from "../common/Icon";
import {withContext} from '../context/AppContext';
import Loader from '../component/Loader';
import Link from '../common/Link';

class Home extends Component {

  state={
    CodeSale: "",
    CollabSale: "",
    TotalShinePoint: "",
    coupon: "",
    shinePoint: "",
    isLoading:true,
    nameForCopy:"Copy"
  }

  componentDidMount() {

    const {setApiCache,getApiCache}=this.props.context;
    if(getApiCache('dashboard')){
      this.setState({...getApiCache('dashboard'),isLoading:false})
    }

    Fetch("dashboard/dashboard.php").then(d=>{
      console.log(d);
      if(d.status){
        setApiCache("dashboard",d.data)
        this.setState({...d.data,isLoading:false})
      }
    }).catch(e=>console.log(e))
  }

  copyText(text) {
    var textarea = document.createElement('textarea');
    textarea.textContent = text;
    document.body.appendChild(textarea);
    var selection = document.getSelection();
    var range = document.createRange();
    range.selectNode(textarea);
    selection.removeAllRanges();
    selection.addRange(range);
    if(document.execCommand('copy')){
        this.setState({nameForCopy:'Copied'})
    }
    selection.removeAllRanges();
    document.body.removeChild(textarea);
}


  render() {

    const {CodeSale,CollabSale,TotalShinePoint,coupon,shinePoint,isLoading,nameForCopy}=this.state;

// console.log(Number(CollabSale)>0);

    return (
      isLoading
      ?
      <Loader/>
      :
      <div className="">
        <h1 className="main_hed">PrepStart Dashboard</h1>
        <div className="row">
          <div className="col-12">
            <div className="benifit_sec">
              <h2 className="sub_hed">Your Shine Meter</h2>
              <div className="progress_bar">
                <span className="bronze">
                  <img src={bronze} alt="" />
                </span>
                <span className="gold">
                  <img src={gold} alt="" />
                </span>
                <span className="silver">
                  <img src={silver} alt="" />
                </span>
                <div style={{width:((Number(shinePoint)/Number(TotalShinePoint)) *100)+"%"}} className="live_progress" />
              </div>
              <p>You are just {TotalShinePoint -  Number(shinePoint)} points away from Gold benefits</p>
            </div>
          </div>
        </div>
        <div className="content_box">
          <div className="row">
            <div className="col-md-6 cstm_mb2">
              <div className="card">
                <h2 className="sub_hed">My Coupon Code </h2>
                <div className="couponCode" onClick={()=>this.copyText(coupon)}>
                  <input type="text" readOnly defaultValue={coupon} className="text-uppercase form-control" />
                  <span className={`copy ${nameForCopy==='Copied' && "green"}`}>{nameForCopy}</span>
                </div>
                <div className="share text-right"><a href="#">Share <img src={share} alt="Share" /></a>
                </div>
              </div>
            </div>
            <div className="col-md-6 cstm_mb2">
              <div className="card">
                <h2 className="sub_hed">Your Lifetime Sales</h2>
                <div className="shine_points lyftym_sale">
                  <table className="shineTable">
                    <tbody>
                      <tr>
                        <th className="pt-0">Group Sales</th>
                          <td className="pt-0"><span className={`small ${Number(CollabSale) > 0 ? "green" :"red"}`}>{ CollabSale }</span></td>
                      </tr>
                      <tr>
                        <th>Individual Sales</th>
                          <td><span className={`small ${Number(CodeSale) > 0 ? "green" :"red"}`}>{CodeSale}</span></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
            <div className="col-md-6 cstm_mb2">
                <SummeryTable/>
            </div>
            <div className="col-md-6 cstm_mb2">
              <PaymentSummaryTable/>
            {/* <div className="card">
                <div className="shine_points">
                  <table className="shineTable">
                    <tbody>
                      <tr>
                        <td className="p-0">
                          <h2 className="sub_hed mb-0">Your Total Shine Points</h2>
                        </td>
                        <td className="green text-center p-0" style={{fontSize:"16px", fontWeight:"700"}}>450</td>
                      </tr>
                      <tr className="border-0">
                        <th>Referral Sign Up Bonus <span>Sec: Sourabh Gupta</span>
                        </th>
                        <td className="text-center">Dec 25, 2019 <span className="small green">+10</span></td>
                      </tr>
                      <tr>
                        <th>Referral Sign Up Bonus <span>Sec: Sourabh Gupta</span></th>
                        <td className="text-center">Dec 25, 2019 <span className="small red">-25</span></td>
                      </tr>
                      <tr>
                        <th>Referral Sign Up Bonus <span>Sec: Sourabh Gupta</span></th>
                        <td className="text-center">Dec 25, 2019 <span className="small green">+10</span></td>
                      </tr>
                      <tr>
                        <th>Referral Sign Up Bonus <span>Sec: Sourabh Gupta</span></th>
                        <td className="text-center">Dec 25, 2019 <span className="small green">+10</span></td>
                      </tr>
                      <tr>
                        <td colSpan={2}><a href="#" className="table_vm">View More</a></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div> */}
            </div>
            
          </div>
        </div>
      </div>
    );
  }
}

export default withContext(Home);


const PaymentSummaryTable=withContext(({context:{setApiCache,getApiCache},})=>{
  const [isLoading,setLoading]=useState(1);
  const [{paymentSummery,totalRecords},setData]=useState({});

  useEffect(()=>{
    // console.log(setApiCache);
    if(getApiCache('paymentsummeryDash')){
      setLoading(0)
      setData(getApiCache('paymentsummeryDash'))
    }
    
    Fetch('payment/paymentsummery.php',{maxPerPage:4,counter:0}).then(d=>{
      console.log(d);
      if(d.status){
        setData(d.data)
        setApiCache('paymentsummeryDash',d.data)
        setLoading(0)
      }
    })
  },[])

  return (
            isLoading
            ?
            <Loader height='100%'/>
            :
            <div className="card">
                <div className="shine_points">
                  <table className="shineTable">
                    <tbody>
                      <tr>
                        <td  className="p-0">
                            <h2 className="sub_hed mb-0">Payments Transaction Log</h2>
                        </td>
                        {/* <td className="green text-center p-0" style={{fontSize:"16px", fontWeight:"700"}}>450</td> */}
                      </tr>
                      {
                        paymentSummery && paymentSummery.length
                        ?
                        paymentSummery.map((d,i)=>(
                          <tr key={i} className="border-0">
                            <th>{d.label}
                            <span>{d.date}</span> </th>
                              <td className="text-center">{d.amountPaid}</td>
                          </tr>
                        ))
                        : null
                      }
                     
                     {
                       Number(totalRecords) > 4
                       ?
                        <tr>
                          <td colSpan={2}><Link to="/paymentlog" className="table_vm">View More</Link></td>
                        </tr>
                        : null
                     }
                      
                    </tbody>
                  </table>
                </div>
              </div>
  )
})

const SummeryTable=withContext(({context:{setApiCache,getApiCache},})=>{
  const [isLoading,setLoading]=useState(1);
  const [{pointSummery,total,totalRecords},setData]=useState({});

  useEffect(()=>{
    // console.log(setApiCache);
    if(getApiCache('pointssummeryDash')){
      setLoading(0)
      setData(getApiCache('pointssummeryDash'))
    }
    
    Fetch('points/pointssummery.php',{maxPerPage:4,counter:0}).then(d=>{
      console.log(d);
      if(d.status){
        setData(d.data)
        setApiCache('pointssummeryDash',d.data)
        setLoading(0)
      }
    })
  },[])

  return (
            isLoading
            ?
            <Loader height='100%'/>
            :
            <div className="card">
                <div className="shine_points">
                  <table className="shineTable">
                    <tbody>
                      <tr>
                        <td  className="p-0">
                            <h2 className="sub_hed mb-0">{total.title}</h2>
                        </td>
                          <td className="green text-center p-0" style={{fontSize:"16px", fontWeight:"700"}}>{total.totalPoint}</td>
                      </tr>
                      {
                        pointSummery && pointSummery.length
                        ?
                        pointSummery.map((d,i)=>(
                          <tr key={i} className="border-0">
                            <th>{d.label}
                            </th>
                        <td className="text-center">{d.date} <span className={`small ${Number(d.points) > 0 ? "green" :"red"}`}>{d.points}</span></td>
                          </tr>
                        ))
                        : null
                      }
                     
                     {
                       Number(totalRecords) > 4
                       ?
                        <tr>
                          <td colSpan={2}><Link to="/shinepoint" className="table_vm">View More</Link></td>
                        </tr>
                        : null
                     }
                      
                    </tbody>
                  </table>
                </div>
              </div>
  )
})
